	module.exports = {"data":{"allTalkJson":{"edges":[{"node":{"description":"This is the second part of Martin Fowlers keynote at OOP 2014 in Munich and is a tricky talk to describe. Usually Martin likes a title and abstract to describe what the talk is about - but this talk is a journey, and he doesn't want to tell you where he's going, but instead to explore the ground with him. Martin Fowler will say that it starts with his biggest problem with most adoption of agile software development - the nature of the interaction between users, analysts, and programmers. It goes on to explore these roles, raising questions about the relationship of programmers to users, our responsibilities to them, and finally the Two Great Challenges that I think programmers need to face up to.","meta":{"duration":"PT24M12S"},"presenter":{"name":"Martin Fowler"},"source":{"id":"Z8aECe4lp44","provider":"youtube"},"slug":"not-just-code-monkeys","tag":["OOP2014","Munich","Food For Thought"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/Z8aECe4lp44/default.jpg","height":90,"width":120}},"title":"\"Not Just Code Monkeys\""}}]}},"pathContext":{"slug":"food-for-thought","tag":"Food For Thought"}}

